<template>
<example-main-attack :my-list="list">
<template #attacker>
  <p class="font-bold text-white font-black w-full">مهاجمان تیم استقلال</p>
</template>
</example-main-attack>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleMainAttack from "@/components/main/ExampleMainAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "MainAttack",
  components: {ExampleMainAttack},
  setup(){
    const list=reactive([])
    const sum=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/1614'),
        axios.get('https://api.footballi.net/api/v2/player/1627'),
        axios.get('https://api.footballi.net/api/v2/player/6259'),
        axios.get('https://api.footballi.net/api/v2/player/6274'),
        axios.get('https://api.footballi.net/api/v2/player/15266'),
        axios.get('https://api.footballi.net/api/v2/player/2225401'),
        axios.get('https://api.footballi.net/api/v2/player/2237681'),
        axios.get('https://api.footballi.net/api/v2/player/2281234'),
        axios.get('https://api.footballi.net/api/v2/player/2371763'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      list,sum,
    }
  }
}
</script>

