<template>
<section class="center-players">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <p class="font-semibold text-start text-black w-full">بازیکنان تیم استقلال</p>
      <div class="bg-black h-0.5 w-full my-1"></div>
      <div class="bg-green-500 w-full min-h-full relative px-3">
        <example-goal-keeper :my-list="menu">
          <template #goalKeeper>
            <p class="font-bold text-white font-black w-full">دروازه بان های تیم استقلال</p>
          </template>
        </example-goal-keeper>
        <defender-esteghlal></defender-esteghlal>
       <attack-esteghlal></attack-esteghlal>
        <main-attack></main-attack>
        <img :src="require('../../../assets/image/squad-middle.png')" alt="" class="h-auto absolute mx-auto bottom-2.5 w-full left-0 right-0">
      </div>
    </div>
  </div>
</section>
</template>

<script>
import axios from 'axios'

import {onMounted,reactive} from 'vue'
import DefenderEsteghlal from "@/components/main/TeamEsteghlal/DefenderEsteghlal";
import AttackEsteghlal from "@/components/main/TeamEsteghlal/AttackEsteghlal";
import MainAttack from "@/components/main/TeamEsteghlal/MainAttack";
import ExampleGoalKeeper from "@/components/main/ExampleGoalKeeper";
import {handleError} from "@/components/composable/handleError";

export default {

  name: "PlayersEsteghlal",
  components: {ExampleGoalKeeper, MainAttack, AttackEsteghlal, DefenderEsteghlal,},
  setup(){
    const menu=reactive([]);
    const result=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/6388'),
        axios.get('https://api.footballi.net/api/v2/player/2371696'),
        axios.get('https://api.footballi.net/api/v2/player/2446198'),
      ])
      .then(axios.spread((...response)=>{
        menu.push(response)
      }))
      .catch((error)=>{
       handleError(error)
      })

    })
    return{
      menu,
      result,
    }
  }
}
</script>

