<template>
<example-attack :my-list="list">
<template #Midfielder>
  <p class="font-bold text-white font-black w-full">هافبک های تیم استقلال</p>
</template>
</example-attack>
</template>

<script>
import axios from 'axios'
import{reactive,onMounted} from'vue'
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "AttackEsteghlal",
  components: {ExampleAttack},
  setup(){
    const list=reactive([]);
    const total=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/6236'),
        axios.get('https://api.footballi.net/api/v2/player/6321'),
        axios.get('https://api.footballi.net/api/v2/player/6351'),
        axios.get('https://api.footballi.net/api/v2/player/18493'),
        axios.get('https://api.footballi.net/api/v2/player/2223623'),
        axios.get('https://api.footballi.net/api/v2/player/2226944'),
        axios.get('https://api.footballi.net/api/v2/player/2226968'),
        axios.get('https://api.footballi.net/api/v2/player/2282210'),
        axios.get('https://api.footballi.net/api/v2/player/2440403'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
           handleError(error)
          })
    })
    return{
      list,total
    }
  }
}
</script>

