            <template #part-one>
              <p>
                فصل بعد، پرسپولیس از نخستین دوره لیگ قهرمانان آسیا حذف شد و در لیگ نتایج خوبی نگرفت. این سال‌ها آغاز دورانی بود که سالنامه باشگاه آن را «دوران جدال با بحران» نامگذاری کرده، تا جایی که پرسپولیس حتی مقام نهم لیگ را هم تجربه کرد. دو فصل بعد، پرسپولیس پس از 6 سال دوری از جام در جام خلیج فارس 87_1386 و با هدایت افشین قطبی، در حالی به قهرمانی رسید که از این تیم 6 امتیاز به علت شکایت رافائل ادریو کسر شده بود.

                پرسپولیس این دهه را با دو قهرمانی متوالی در جام حذفی (89_1388 و 90_1389) با هدایت علی دایی به پایان برد.

                پرسپولیس دهه نود را با حمید استیلی آغاز کرد و خیلی زود جایش را با دنیزلی تعویض کرد. یازدهمین دوره جام خلیج فارس برای پرسپولیس با رتبه دوازدهمی به پایان رسید. دنیزلی در نیم فصل سال ۱۳۹۰ با قراردادی یک سال و نیمه به باشگاه فوتبال پرسپولیس پیوست. اما در تیرماه 91 بخاطر مشکلات خانوادگی از این سمت استعفا کرد. در سال های ملتهب پرسپولیس، لیگ دوازدهم با مانوئل ژوزه شروع و در انتهای فصل با یحیی گل محمدی در رتبه هفتم به پایان رسید و پس از تساوی مقابل نفت تهران در هفته بیست و پنجم، از پرسپولیس کنار گذاشته شد.

                علی دایی و حمید درخشان نیز نتوانستند نتایج خوبی کسب کنند. هنگامی که پرسپولیس نیز همانند بسیاری از باشگاه‌های دیگر ایرانی در بحران بود، نژادفلاح مدیرعامل وقت پرسپولیس، به استانبول رفت و با برانکو ایوانکوویچ وارد مذاکره شد؛ در نهایت باشگاه پرسپولیس با توجه به قول‌های حسین هدایتی جهت پرداخت مبلغ قرارداد برانکو، با او به توافق رسید و برانکو ایوانکوویچ سرمربی تیم فوتبال پرسپولیس شد. چهلمین سرمربی تاریخ باشگاه پرسپولیس، در ابتدا نتایج قابل قبولی با سرخپوشان به دست نیاورد و در شش بازی نخست در لیگ، تنها 4 امتیاز کسب کرد، اما با حمایت هوادارن و ارائه بازی قابل قبول و رکورد 21 بازی بدون شکست، تا یک قدمی قهرمانی پیش رفت ولی در هفته آخر با تفاضل گل، قهرمانی را به استقلال خوزستان واگذار کرد. مهم ترین اتفاق این سال، در گذشت هادی نوروزی بود.
                رقيبان اصلى اين باشگاه در فوتبال تهران، دارايى، سرباز و شاهين بودند كه پيش از تأسيس دوچرخه سواران فوتبال باشگاهى در انحصار آنها بود.

                آنها به معناى واقعى باشگاه بودند و در چندين رشته ورزشى فعاليت مى كردند. اما اولين حضور رسمى آنها در سال 1325 و با كسب دو عنوان نايب قهرمانى در جام باشگاههاى تهران و جام حذفى تهران همراه بود.

                در سال 1328 پرویز خسروانی امتیاز باشگاه دوچرخه سواران را به نام خود به ثبت رساند و به دليل اينكه باشگاه در 18 رشته ورزشى فعاليت داشت و عموم مردم تصور مى كردند فقط باشگاه دوچرخه سواران در رشته دوچرخه سوارى فعاليت دارد، نام اين باشگاه با نظر هيئت مديره به تاج تغيير يافت.

                با توجه به اختلافات درونی باشگاه در دهه 30، این باشگاه چندین فصل در مسابقات باشگاه های تهران شرکت نکرد. در نیمه دوم این دهه و بازگشت علی دانایی فر، تاج در سال 1335 و 1337 توانست هر دو جام باشگاه ها و جام حذفی را به دست آورد. دانایی‌فر این بار به مدت سیزده سال بی‌وقفه مربی‌گری تاج را بر عهده داشت تا صاحب رکورد طولانی‌ترین دوران مربی‌گری در پیشینه این تیم شود.
