            <template #part-two>
              <p>
                در روزهای عدم برگزاری لیگ سراسری، در نیمه نخست دهه شصت، استقلال دو بار در سال‌های 1362 و 1364 جام باشگاه‌های تهران را فتح کرد که قهرمانی این تیم در فصل 62 نخستین قهرمانی‌اش با نام نوین استقلال بود. پس از قهرمانی در چهارمین دوره لیگ قدس -نخستین مسابقات سراسری باشگاهی در ایران پس از انقلاب- و شکست پرسپولیس در فینال، در 1369 به عنوان نماینده ایران راهی جام باشگاه‌های آسیا 91_1990 شدند و در حالی که این مسابقات به دلیل جنگ خلیج فارس به تعویق افتاده بود،سرانجام در تابستان 1370 مرحله نهایی‌اش به میزبانی بنگلادش برگزار شد و استقلال در دیدار فینال با پیروزی برابر انینگ چین  با گل های رضا حسن زاده و صمد مرفاوی موفق شد برای دومین بار قهرمان جام باشگاه‌های آسیا شود. در آن دوره از مسابقات که استقلال با هدایت منصور پورحیدری عنوان قهرمانی را تصاحب کرد، مرفاوی با شش گل زده آقای گل مسابقات شد. نامجو مطلق تکنیکی‌ترین بازیکن جام لقب گرفت، عباس سرخاب به عنوان مرد سال آسیا شناخته شد و عابدزاده جایزه بهترین دروازه‌بان جام را کسب کرد.
                سال بعد استقلال یک بار دیگر حضور در فینال جام باشگاه‌های آسیا را تجربه کرد که در گام آخر در ضربات پنالتی مغلوب تیم الهلال عربستان سعودی شد و از دست‌یابی به سومین قهرمانی قاره‌ای بازماند. این تیم پس از قهرمانی آسیا روند رو به سقوط را آغاز کرد و با قانون فدراسیون فوتبال در دوران ناصر نوآموز، به دسته سوم سقوط کرد. فدراسيون فوتبال در سال 71 تصميم گرفت براي رونق فوتبال در كشور به استانها سهميه بدهد. سهميه تهران چهار تيم بود كه دو تيم پاس و پرسپولیس به عنوان قهرمان و نايب قهرمان ليگ، دو سهميه اول تهران را تشكيل مي دادند. براي انتخاب دو تيم ديگر يك دوره مسابقات هشت جانبه برگزار شد که در پايان اين دوره از بازيها كه در زمستان 71 انجام شد؛ دو تيم سايپا و كشاورز به همراه پرسپوليس و پاس كه حضورشان از قبل قطعي شده بود جواز حضور در ليگ سال 72 را گرفتند و استقلال كه در جمع هشت تيم با پنج تساوي و دو باخت در مكان هفتم قرار گرفته بود مجبور بود بين حضور در جام باشگاههاي تهران و ليگ دسته سوم يكي را انتخاب كند كه اوليايي مديرعامل وقت استقلال حضور در دسته سوم را انتخاب كرد.در بهار 73 مسابقاتي هفت جانبه تحت عنوان جام برتر باشگاههاي تهران برگزار شد كه در پايان اين بازيها استقلال با كسب عنوان قهرماني مجددا راهي دسته اول شد. در نیمه دوم این دهه نیز استقلال یک بار دیگر و این بار در مقابل تیم ژاپنی جوبیلو ایواتا در بازی فینال مغلوب شد و برای دومین بار به مقام نایب قهرمانی جام باشگاه‌های آسیا رسید
                پس از ناکامی متوالی مربیان ایرانی در هدایت این تیم به عنوان قهرمانی لیگ برتر ایران، در میانه دهه نود سرانجام مدیران باشگاه استقلال سیاست استخدام بازیکنان سابق باشگاه به عنوان مربی را کنار گذاشتند و بعد از پانزده سال یک مربی خارجی، وینفرد شفر آلمانی را به عنوان مربی این تیم برگزیدند و وی موفق شد تا در نخستین فصل حضورش در استقلال عنوان قهرمانی در جام حذفی را برای این تیم به ارمغان آورد. دوران حضور شفر در استقلال کوتاه بود و به دنبال نتایج ضعیف با استقلال در دومین فصل حضورش از سمت خود برکنار شد تا آندره‌آ استراماچونی ایتالیایی جانشین وی شود و به این ترتیب برای نخستین بار یک مربی که سابقه مربی‌گری در سری آ را دارد در استقلال مشغول به کار شود.
              </p>
