<template>
<example-super-goal :my-list="players">

</example-super-goal>
</template>

<script>
import axios from 'axios'
import {reactive,onMounted} from 'vue'
import ExampleSuperGoal from "@/components/main/ExampleSuperGoal";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "SupergoalEsteghlal",
  components: {ExampleSuperGoal},
  setup(){
    const players=reactive([])
    const total=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/2226944'),
        axios.get('https://api.footballi.net/api/v2/player/2226968'),
        axios.get('https://api.footballi.net/api/v2/player/2281234'),
        axios.get('https://api.footballi.net/api/v2/player/1627'),
      ])
      .then(axios.spread((...response)=>{
        players.push(response)
      }))
      .catch((error)=>{
   handleError(error)
      })

    })
    return{
      players,
      total,

    }
  }
}
</script>

