<template>
<example-transfer :my-list="menu">

</example-transfer>
</template>

<script>
import{reactive,onMounted} from 'vue'
import axios from 'axios'
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "TransferTeamEseghlal",
  components: {ExampleTransfer},
  setup(){
    const menu=reactive([])
    const sum=onMounted(async()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/6455'),
        axios.get('https://api.footballi.net/api/v2/player/2226054'),
        axios.get('https://api.footballi.net/api/v2/player/2440418'),
        axios.get('https://api.footballi.net/api/v2/player/1597'),
        axios.get('https://api.footballi.net/api/v2/player/2226944'),
        axios.get('https://api.footballi.net/api/v2/player/2281234'),
        axios.get('https://api.footballi.net/api/v2/player/6236'),
        axios.get('https://api.footballi.net/api/v2/player/1583'),
        axios.get('https://api.footballi.net/api/v2/player/29946'),
        axios.get('https://api.footballi.net/api/v2/player/2226054'),
      ])
          .then(axios.spread((...response)=>{
            menu.push(response)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })

    return{
      menu,sum
    }
  }
}
</script>

<style scoped>
.scroll{
  overflow-x: hidden;
  overflow-y: scroll;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color:gray;
}
#scroll::-webkit-scrollbar-track{
  background-color: lightgray;
  box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}
</style>