<template>
<example-defender :my-list="list">
<template #defender>
  <p class="font-bold text-white font-black w-full">مدافعان تیم استقلال</p>
</template>
</example-defender>
</template>

<script>
import axios from 'axios'
import{reactive,onMounted} from 'vue'
import ExampleDefender from "@/components/main/ExampleDefender";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "DefenderEsteghlal",
  components: {ExampleDefender},
  setup(){
    const list=reactive([])
    const result=onMounted(async ()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/1597'),
         axios.get('https://api.footballi.net/api/v2/player/2222897'),
         axios.get('https://api.footballi.net/api/v2/player/2258214'),
         axios.get('https://api.footballi.net/api/v2/player/2259517'),
         axios.get('https://api.footballi.net/api/v2/player/2371753'),
      ])
      .then(axios.spread((...response)=>{
        list.push(response)
      }))
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
    result,list
    }
  }
}
</script>

