<template>
<example-team-news :my-list="list">

</example-team-news>
</template>

<script>
import{reactive,onMounted} from 'vue'
import EndPoints from "@/service/EndPoints";
import ExampleTeamNews from "@/components/main/ExampleTeamNews";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "LastNewsEsteghlal",
  components: {ExampleTeamNews},
  setup(){
    const list=reactive([])
    const menu=reactive([])
    const total=onMounted(()=>{
      EndPoints.getEsteghlalOne()
      .then((response)=>{
        if(response.data.success){
          menu.push(response.data.data)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    const sum=onMounted(()=>{
      EndPoints.getEsteghlalTwo()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data)
        }
      })
      .catch((error)=>{
       handleError(error)
      })

    })
    return{
      sum,
      list,
      total,

    }
  }
}
</script>

<style scoped>
.scroll{
  overflow-x:hidden;
  overflow-y:scroll;
  background-color:#fff;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color:gray;
}
#scroll::-webkit-scrollbar-track{
  background-color:lightgray;
  box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}
</style>